import {validateIp, map, getAddress} from "./helpers";

const ipInput = document.querySelector(".search-bar__input")
const btn = document.querySelector("button")

const ipInfo = document.querySelector("#ip")
const locationInfo = document.querySelector("#location")
const timeZoneInfo = document.querySelector("#timezone")
const ispInfo = document.querySelector("#isp")

document.addEventListener("DOMContentLoaded", () => {
    getAddress("77.51.199.15").then(setInfo)
})

btn.addEventListener("click", getData)
ipInput.addEventListener("keydown", handleKey)

function getData() {

    if (validateIp(ipInput.value)) {
        getAddress(ipInput.value).then(setInfo)
    }
}

function handleKey(e) {
   if(e.key === "Enter") {
       getData()
   }
}

function setInfo(mapData) {
    ipInfo.innerText = mapData.ip_address
    locationInfo.innerText = `${mapData.region} ${mapData.city}`
    timeZoneInfo.innerText = mapData.timezone.name
    ispInfo.innerText = mapData.connection.autonomous_system_organization
    map([mapData.latitude, mapData.longitude])
}









