
let myMap, myPlacemark

export function map(point) {
    ymaps.ready(init);

    function init(){

        if(myMap){
            myMap.destroy();
            myMap = null;
        }

        myMap = new ymaps.Map("map", {
            center: point,
            zoom: 7
        });



        myPlacemark = new ymaps.Placemark(point)

        myMap.geoObjects.add(myPlacemark)
    }

}

